import Vue from 'vue'
import VueRouter from 'vue-router'
import Index from '../views/index.vue'
import Error_Template from '../views/error/template.vue'
import Not_Access from '../views/error/403.vue'
import Not_Found from '../views/error/404.vue'
import Not_Available from '../views/error/500.vue'

Vue.config.productionTip = false

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: Index,
    name: 'Index',
    meta: { requiresAuth: false }
  },
  {
    path: '/error',
    component: Error_Template,
    redirect: '/system/index',
    children: [
      {
        path: "403",
        component: Not_Access,
        name: "Not_Access",
        meta: { requiresAuth: false }
      },
      {
        path: "404",
        component: Not_Found,
        name: "Not_Found",
        meta: { requiresAuth: false }
      },
      {
        path: "500",
        component: Not_Available,
        name: "Not_Available",
        meta: { requiresAuth: false }
      }
    ]
  },
  {
    path: '/*',
    redirect: '/error/404'
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router